/* eslint-disable @typescript-eslint/no-unused-vars */
'use client';

import React, { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/hooks/useAuth';
import { eventsApi } from '@/lib/api';
import { 
    Calendar, 
    MapPin, 
    Users, 
    Plus, 
    Edit, 
    Trash2, 
    Eye, 
    EyeOff,
    Search,
    Filter,
    Globe,
    Clock,
    Tag,
    DollarSign
} from 'lucide-react';

// Use the same Event interface as your dashboard
interface Event {
    eventDate: string | number | Date;
    revenue: number;
    eventId: number;
    title: string;
    description: string;
    shortDescription?: string;
    organizerId: number;
    organizerName: string;
    venueId: number;
    venueName: string;
    venueCity: string;
    categoryId: number;
    categoryName: string;
    startDateTime: string;
    endDateTime: string;
    imageUrl?: string;
    basePrice: number;
    currency: string;
    isOnline: boolean;
    ticketsSold: number;
    availableTickets: number;
    status: string;
    isPublished: boolean;
}

const OrganizerEventsPage = () => {
    const router = useRouter();
    const { user, isOrganizer } = useAuth();
    
    const [events, setEvents] = useState<Event[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState('');
    const [searchTerm, setSearchTerm] = useState('');
    const [filterPublished, setFilterPublished] = useState<'all' | 'published' | 'unpublished'>('all');

    // Load user's events
    useEffect(() => {
        if (user && isOrganizer) {
            fetchMyEvents();
        } else if (user && !isOrganizer) {
            router.push('/');
        }
    }, [user, isOrganizer, router]);

    const fetchMyEvents = async () => {
        try {
            setLoading(true);
            setError('');

            const response = await fetch('http://localhost:5251/api/events/my-events', {
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('authToken')}`
                }
            });

            if (!response.ok) {
                throw new Error('Failed to fetch your events');
            }

            const eventsData = await response.json();
            console.log('Loaded my events:', eventsData);
            
            // Debug: Check date formats and all properties
            if (eventsData.length > 0) {
                // Temporarily disable debug logs to reduce console noise
                // console.log('Sample event date format:', {
                //     eventDate: eventsData[0].eventDate,
                //     endDate: eventsData[0].endDate,
                //     createdAt: eventsData[0].createdAt
                // });
                
                // console.log('Full first event with all properties:');
                // console.log(JSON.stringify(eventsData[0], null, 2));
                
                // For now, just log that events were loaded
                console.log(`Successfully loaded ${eventsData.length} events`);
            }
            
            setEvents(eventsData);

        } catch (error) {
            console.error('Error fetching my events:', error);
            setError('Failed to load your events. Please try again.');
        } finally {
            setLoading(false);
        }
    };

    const handlePublishToggle = async (eventId: number, isCurrentlyPublished: boolean) => {
        try {
            const action = isCurrentlyPublished ? 'unpublish' : 'publish';
            const response = await fetch(`http://localhost:5251/api/events/${eventId}/${action}`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('authToken')}`
                }
            });

            if (!response.ok) {
                throw new Error(`Failed to ${action} event`);
            }

            // Update the event in the local state
            setEvents(prevEvents => 
                prevEvents.map(event => 
                    event.eventId === eventId 
                        ? { ...event, isPublished: !isCurrentlyPublished }
                        : event
                )
            );

        } catch (error) {
            console.error('Error toggling publish status:', error);
            setError(`Failed to ${isCurrentlyPublished ? 'unpublish' : 'publish'} event`);
        }
    };

    const handleDeleteEvent = async (eventId: number, eventTitle: string) => {
        if (!confirm(`Are you sure you want to delete "${eventTitle}"? This action cannot be undone.`)) {
            return;
        }

        try {
            const response = await fetch(`http://localhost:5251/api/events/${eventId}`, {
                method: 'DELETE',
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('authToken')}`
                }
            });

            if (!response.ok) {
                throw new Error('Failed to delete event');
            }

            // Remove the event from local state
            setEvents(prevEvents => prevEvents.filter(event => event.eventId !== eventId));

        } catch (error) {
            console.error('Error deleting event:', error);
            setError('Failed to delete event. Please try again.');
        }
    };

    // Filter events based on search and publish status
    const filteredEvents = events.filter(event => {
        if (!searchTerm.trim()) {
            // If no search term, only apply publish filter
            return filterPublished === 'all' || 
                   (filterPublished === 'published' && event.isPublished) ||
                   (filterPublished === 'unpublished' && !event.isPublished);
        }
        
        // Case-insensitive search across multiple fields
        const searchLower = searchTerm.toLowerCase();
        const matchesSearch = 
            event.title?.toLowerCase().includes(searchLower) ||
            event.description?.toLowerCase().includes(searchLower) ||
            event.shortDescription?.toLowerCase().includes(searchLower) ||
            event.venueName?.toLowerCase().includes(searchLower) ||
            event.venueCity?.toLowerCase().includes(searchLower) ||
            event.categoryName?.toLowerCase().includes(searchLower) ||
            event.organizerName?.toLowerCase().includes(searchLower) ||
            // Search in formatted date
            formatDate(event.startDateTime || event.eventDate).toLowerCase().includes(searchLower) ||
            // Search for "online" or "in-person"
            (event.isOnline && 'online'.includes(searchLower)) ||
            (!event.isOnline && ('in-person'.includes(searchLower) || 'physical'.includes(searchLower))) ||
            // Search in ticket and revenue info
            event.ticketsSold?.toString().includes(searchTerm) ||
            event.currency?.toLowerCase().includes(searchLower) ||
            // Search in status
            (event.isPublished && ('published'.includes(searchLower) || 'live'.includes(searchLower))) ||
            (!event.isPublished && ('draft'.includes(searchLower) || 'unpublished'.includes(searchLower)));
        
        const matchesFilter = filterPublished === 'all' || 
                            (filterPublished === 'published' && event.isPublished) ||
                            (filterPublished === 'unpublished' && !event.isPublished);
        
        return matchesSearch && matchesFilter;
    });

    const formatDate = (dateString: string | number | Date) => {
        if (!dateString) return 'Date not set';
        
        try {
            const date = new Date(dateString);
            if (isNaN(date.getTime())) return 'Invalid date';
            
            return date.toLocaleDateString('en-US', {
                year: 'numeric',
                month: 'short',
                day: 'numeric',
                hour: '2-digit',
                minute: '2-digit',
                timeZone: 'local'
            });
        } catch (error) {
            return 'Date error';
        }
    };

    if (!user || !isOrganizer) {
        return (
            <div className="min-h-screen bg-gray-50 flex items-center justify-center">
                <div className="text-center">
                    <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
                    <p className="mt-4 text-gray-600">Loading...</p>
                </div>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-gray-50">
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
                {/* Header */}
                <div className="mb-8">
                    <div className="flex justify-between items-center">
                        <div>
                            <h1 className="text-3xl font-bold text-gray-900">My Events</h1>
                            <p className="text-gray-600 mt-1">Manage your events and track their performance</p>
                        </div>
                        <button
                            onClick={() => router.push('/organizer/events/create')}
                            className="flex items-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
                        >
                            <Plus className="h-5 w-5 mr-2" />
                            Create Event
                        </button>
                    </div>
                </div>

                {/* Search and Filters */}
                <div className="mb-6 flex flex-col sm:flex-row gap-4">
                    <div className="flex-1 relative">
                        <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" />
                        <input
                            type="text"
                            placeholder="Search by title, venue, category, date, status, revenue..."
                            value={searchTerm}
                            onChange={(e) => setSearchTerm(e.target.value)}
                            className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                        />
                    </div>
                    <div className="flex items-center space-x-2">
                        <Filter className="h-5 w-5 text-gray-400" />
                        <select
                            value={filterPublished}
                            onChange={(e) => setFilterPublished(e.target.value as 'all' | 'published' | 'unpublished')}
                            className="px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                        >
                            <option value="all">All Events</option>
                            <option value="published">Published</option>
                            <option value="unpublished">Unpublished</option>
                        </select>
                    </div>
                </div>

                {/* Error Message */}
                {error && (
                    <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-lg">
                        <p className="text-red-700 text-sm">{error}</p>
                        <button 
                            onClick={() => setError('')}
                            className="text-red-500 text-xs underline mt-1"
                        >
                            Dismiss
                        </button>
                    </div>
                )}

                {/* Loading State */}
                {loading ? (
                    <div className="flex items-center justify-center py-12">
                        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
                        <p className="ml-3 text-gray-600">Loading your events...</p>
                    </div>
                ) : (
                    <>
                        {/* Events Stats */}
                        <div className="mb-6 grid grid-cols-1 md:grid-cols-3 gap-4">
                            <div className="bg-white p-4 rounded-lg border border-gray-200">
                                <div className="text-2xl font-bold text-gray-900">{events.length}</div>
                                <div className="text-sm text-gray-600">Total Events</div>
                            </div>
                            <div className="bg-white p-4 rounded-lg border border-gray-200">
                                <div className="text-2xl font-bold text-green-600">
                                    {events.filter(e => e.isPublished).length}
                                </div>
                                <div className="text-sm text-gray-600">Published</div>
                            </div>
                            <div className="bg-white p-4 rounded-lg border border-gray-200">
                                <div className="text-2xl font-bold text-orange-600">
                                    {events.filter(e => !e.isPublished).length}
                                </div>
                                <div className="text-sm text-gray-600">Unpublished</div>
                            </div>
                        </div>

                        {/* Events List */}
                        {filteredEvents.length === 0 ? (
                            <div className="text-center py-12">
                                <Calendar className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                                <h3 className="text-lg font-medium text-gray-900 mb-2">
                                    {events.length === 0 ? 'No events created yet' : 'No events match your search'}
                                </h3>
                                <p className="text-gray-600 mb-4">
                                    {events.length === 0 
                                        ? 'Create your first event to get started with EventHub'
                                        : 'Try adjusting your search or filter criteria'
                                    }
                                </p>
                                {events.length === 0 && (
                                    <button
                                        onClick={() => router.push('/organizer/events/create')}
                                        className="inline-flex items-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
                                    >
                                        <Plus className="h-5 w-5 mr-2" />
                                        Create Your First Event
                                    </button>
                                )}
                            </div>
                        ) : (
                            <div className="space-y-4">
                                {filteredEvents.map((event) => (
                                    <div key={event.eventId} className="bg-white border border-gray-200 rounded-lg shadow-sm hover:shadow-md transition-shadow">
                                        <div className="p-6">
                                            <div className="flex justify-between items-start">
                                                <div className="flex-1">
                                                    <div className="flex items-center space-x-3 mb-2">
                                                        <h3 className="text-xl font-semibold text-gray-900">{event.title}</h3>
                                                        <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                                                            (event.isPublished || false)
                                                                ? 'bg-green-100 text-green-800' 
                                                                : 'bg-orange-100 text-orange-800'
                                                        }`}>
                                                            {(event.isPublished || false) ? 'Published' : 'Draft'}
                                                        </span>
                                                    </div>
                                                    
                                                    <p className="text-gray-600 mb-4 line-clamp-2">
                                        {event.description || event.shortDescription || 'No description available'}
                                    </p>
                                                    
                                                    <div className="flex flex-wrap items-center gap-4 text-sm text-gray-500">
                                                        <div className="flex items-center">
                                                            <Calendar className="h-4 w-4 mr-1" />
                                                            {formatDate(event.eventDate)}
                                                        </div>
                                                        <div className="flex items-center">
                                                            <MapPin className="h-4 w-4 mr-1" />
                                                            {event.isOnline ? 'Online Event' : event.location}
                                                        </div>
                                                        <div className="flex items-center">
                                                            <Users className="h-4 w-4 mr-1" />
                                                            Max {event.maxCapacity} attendees
                                                        </div>
                                                        {event.categoryName && (
                                                            <div className="px-2 py-1 bg-gray-100 rounded text-xs">
                                                                {event.categoryName}
                                                            </div>
                                                        )}
                                                    </div>
                                                </div>
                                                
                                                {/* Actions */}
                                                <div className="flex items-center space-x-2 ml-4">
                                                    <button
                                                        onClick={() => handlePublishToggle(event.eventId, event.isPublished)}
                                                        className={`p-2 rounded-lg transition-colors ${
                                                            event.isPublished
                                                                ? 'text-orange-600 hover:bg-orange-50'
                                                                : 'text-green-600 hover:bg-green-50'
                                                        }`}
                                                        title={event.isPublished ? 'Unpublish event' : 'Publish event'}
                                                    >
                                                        {event.isPublished ? <EyeOff className="h-5 w-5" /> : <Eye className="h-5 w-5" />}
                                                    </button>
                                                    
                                                    <button
                                                        onClick={() => router.push(`/organizer/events/${event.eventId}/edit`)}
                                                        className="p-2 text-blue-600 hover:bg-blue-50 rounded-lg transition-colors"
                                                        title="Edit event"
                                                    >
                                                        <Edit className="h-5 w-5" />
                                                    </button>
                                                    
                                                    <button
                                                        onClick={() => handleDeleteEvent(event.eventId, event.title)}
                                                        className="p-2 text-red-600 hover:bg-red-50 rounded-lg transition-colors"
                                                        title="Delete event"
                                                    >
                                                        <Trash2 className="h-5 w-5" />
                                                    </button>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        )}
                    </>
                )}
            </div>
        </div>
    );
};

export default OrganizerEventsPage;